import React, {useState, useEffect} from 'react'
import { Link, useParams } from 'react-router-dom'

const BlogDetails = () => {

    const { id } = useParams()
    const [ blog, setBlog ] = useState({})
    const capitalize = (text) => {
        if(text){
            return text[0].toUpperCase() + text.slice(1, )
        }
    }

    const getContent = () => {
        return {__html: blog.content};
      }


    useEffect(() => {
        fetch(`${process.env.REACT_APP_BASE_URL}/api/blog/${id}`)
            .then(res => res.json())
            .then(data => setBlog(data))
    }, [id])

    console.log(blog)

    return(
    <div className='container mt-3'>
        <h1 className='display-2'>
            {blog.title && blog.title.toUpperCase()}
        </h1>
        <h2 className='text-muted mt-3'>
            Category : {capitalize(blog.category)}
        </h2>
        <h4>
            {blog.month} {blog.day}
        </h4>
        <div className='mt-5 mb-5'>
            {
                blog &&
                <article
                dangerouslySetInnerHTML={getContent()}
                />
            }
        </div>
        <hr />
        <p className='lead mb-5'>
            <Link to='/blog' className='font-weight-bold'>
                Back To Blogs
            </Link>
        </p>
    </div>
)}

export default BlogDetails