import React, {useState, useEffect} from 'react'
import { Link } from 'react-router-dom'

const Blog = () => {

    const [ blogs, setBlogs ] = useState([])
    const [ featuredBlog, setFeaturedBlog ] = useState([])

    const capitalize = (text) => {
        if(text){
            return text[0].toUpperCase() + text.slice(1, )
        }
    }

    const getBlogs = () => {
        const blogsWithStyle = blogs.map(blog => (
            <div key={blog.id}
                className="row g-0 border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
                <div className="col p-4 d-flex flex-column position-static">
                    <strong className="d-inline-block mb-2 text-primary">{capitalize(blog.category)}</strong>
                    <h3 className="mb-0">{capitalize(blog.title)}</h3>
                    <div className="mb-1 text-muted">{blog.month} {blog.day}</div>
                    <p className="card-text mb-auto">{blog.excerpt}</p>
                    <Link to={`/blog/${blog.slug}`} className="stretched-link">Continue reading</Link>
                </div>
                <div className="col-auto d-none d-lg-block p-4">
                    <img
                        src={blog.thumbnail}
                        width="200"
                        height='200'
                        alt=""
                    />
                </div>
            </div>
        ))

        let cardBlogs = []
        for(let i=0; i<blogsWithStyle.length; i+=2){
            cardBlogs.push(
                <div key={i} className='row mb-2'>
                    <div className='col-md-6'>
                        {blogsWithStyle[i]}
                    </div>
                    {
                        i < blogsWithStyle.length - 1 &&
                        <div className='col-md-6'>
                            {blogsWithStyle[i+1]}
                        </div>
                    }
                </div>
            )
        }

        return cardBlogs

    }

    useEffect(() => {

        fetch(`${process.env.REACT_APP_BASE_URL}/api/blog/featured`)
            .then(res => res.json())
            .then(data => setFeaturedBlog(data[0]))
            .catch(err => console.log(err))

        fetch(`${process.env.REACT_APP_BASE_URL}/api/blog/`)
            .then(res =>  res.json())
            .then(data => setBlogs(data))
            .catch(err => console.log(err))

    }, [])



    return(
        <div className='container mt-3'>
            <div className="nav-scroller py-1 mb-2">
                <nav className="nav d-flex justify-content-between">
                    <Link className="p-2 text-muted" to="/category/world">World</Link>
                    <Link className="p-2 text-muted" to="/category/environment">Environment</Link>
                    <Link className="p-2 text-muted" to="/category/technology">Technology</Link>
                    <Link className="p-2 text-muted" to="/category/design">Design</Link>
                    <Link className="p-2 text-muted" to="/category/culture">Culture</Link>
                    <Link className="p-2 text-muted" to="/category/business">Business</Link>
                    <Link className="p-2 text-muted" to="/category/politics">Politics</Link>
                    <Link className="p-2 text-muted" to="/category/opinion">Opinion</Link>
                    <Link className="p-2 text-muted" to="/category/science">Science</Link>
                    <Link className="p-2 text-muted" to="/category/health">Health</Link>
                    <Link className="p-2 text-muted" to="/category/style">Style</Link>
                    <Link className="p-2 text-muted" to="/category/travel">Travel</Link>
                </nav>
            </div>
            <div className="jumbotron p-4 p-md-5 mb-4 text-white rounded bg-dark">
                <div className="col-md-6 px-0">
                    <h1 className="display-4 fst-italic">
                        {featuredBlog.title}
                    </h1>
                    <p className="lead my-3">
                        {featuredBlog.excerpt}
                    </p>
                    <p className="lead mb-0">
                        <Link to={`/blog/${featuredBlog.slug}`} className="text-white fw-bold">
                            Continue reading...
                        </Link>
                    </p>
                </div>
            </div>
                {
                    getBlogs()
                }
        </div>
)}

export default Blog